.content {
  font-size: 1.125rem;
  line-height: 1.5;
  //margin-top: 40px;
  h1, h2, h3, h4 {
    font-weight: 700;
  }
  h1 {
    font-size: 3rem;
  }
  h2 {
    font-size: 1.75rem;
    font-weight: 400;
  }
  h3 {
    font-size: 1.5rem;
    font-weight: 600;
  }
  h4 {
    font-size: 1rem;
  }

  img {
    //width: 100%;
  }

  .small-text {
    font-size: .875rem;
  }
  .green-text {
    color: $green;
  }
  .blue-text {
    color: $mid-blue;
  }

  .callout {
    font-size: 1.7rem;
    line-height: 1.2;
    font-weight: 300;
    color: $orange;
    margin: .5em 0;
  }

  main {
    @include clearfix;
    padding: 0;
    margin: 0 auto;
    position: relative;
    /*
    max-width: $screen-md-min !important;
    .single &, .blog &, .archive & {
      max-width: $screen-lg-min !important;
    }
    */
    h1 {
      font-weight: 300;
      margin-bottom: .5em;
    }
    .page-header {
      position: relative;
      color: $white;
      .page-image {
        height: 145px;
        width: 100vw;
        margin-left: calc((100% - 100vw) / 2);
        position: relative;
        img {
          object-fit: cover;
          height: 100%;
          width: 100%;
        }
      }
      .intro {
        position: relative;
        font-size: 1.3rem;
      }
      h1 {
        font-size: 1.5rem;
        color: $white;
        font-weight: 300;
        margin-bottom: .5em;
      }
    }

    .content-container {
      max-width: $screen-md-min;
      margin: 40px auto;
    }

    a.excerpt-more {
      display: block;
      font-weight: 600;
      &::after {
        content: "Â»";
        padding-left: 5px;
      }
    }

    .align-right {
      margin-left: 20px;
    }
    figure {
      max-width: 100%;
    }
    figcaption {
      line-height: normal;
    }
    .quote {
      font-style: italic;
      margin-bottom: 20px;
      line-height: 1.25;
    }
    .quote-attribute {
      margin-top: 10px;
    }

    .btn-primary {
      min-width: 300px;
    }

    // image grid styling
    .image-grid {
      margin-top: 20px;
      > p, > div {
        &[class*="col"] {
          //margin-bottom: 30px;
          padding: 20px 0;
          border-top: 1px $dark-gray solid;
          margin: 0 15px;
          //stext-align: center;
        }
      }
      img {
        //display: block;
        padding-bottom: 10px;
        &.logo {
          height: 75px;
          //max-width: 250px;
          width: auto;
          margin: 0 auto;
        }
      }
      a {
        &:hover {
          color: $dark-blue;
          img {
            opacity: .5;
          }
        }
      }
    }


    // news content styling
    .news-list {
      article {
        margin-bottom: 20px;
      }
    }

    // people/staff content styling
    .people, .people-list {
      .row {
        margin-bottom: 30px;
      }
      img {
        width: 100%;
      }
      .position {
        font-weight: 700;
      }
      .bio {
        margin-top: 20px;
      }
    }
  }

  // timeline
  .horizontal-timeline {
    .events {
      a.selected {
        &::after {
          background-color: $blue;
        }
      }
    }
    .filling-line {
      background-color: $blue;
    }
    .timeline-navigation {
      top: 8px;
      a {
        &:hover {
          text-decoration: none;
        }
      }
    }
    .events-content {
      li {
        list-style: none;
        text-align: left;
        border-radius: 0;
        padding: 20px;
        font-size: 1em;
        background-color: $light-gray;
        color: $dark-gray;
        width: 100%;
        max-width: $screen-sm-min;
        h4 {
          font-size: 1.5rem;
          font-weight: 400;
          text-align: center;
        }
      }
    }
  }
  .background-blue {
    .horizontal-timeline {
      .events {
        background: $light-gray;
        a {
          &::after {
            background-color: $light-gray;
          }
        }
      }
    }
  }

  // submenu navigation
  .submenu-navigation {
    margin-bottom: 40px;
    .menu-submenu > li > a {
      color: $gray;
      font-size: 1.5rem;
      padding-left: 20px;
    }
    ul {
      padding: 0;
      margin: 0;
      li {
        list-style: none;
        font-size: 1.25rem;
      }

      &.menu-submenu > li > .sub-menu {
        background-color: $blue;
        border-radius: 10px;
        padding: 20px 0;
        li {
          //padding: 5px 20px;
          a {
            color: $white;
            font-weight: 600;
            padding: 5px 20px;
            display: block;
          }
          &.current-menu-item > a {
            background-color: $dark-blue;
            &:hover {
              text-decoration: none;
            }
          }
          li {
            a {
              font-weight: 400;
              padding-left: 35px;
            }
            li {
              a {
                font-weight: 300;
                padding-left: 50px;
              }
            }
          }
        }
      }
    }
  }

  .home & {
    main {
      .page-header {
        display: none;
      }
      .content-container {
        margin: 0;
      }
    }
  }

  @include media-breakpoint-down(sm) {
    main {
      .page-header {
        h1 {
          position: absolute;
          top: 20px;
          margin-top: 0;
        }
        .intro {
          color: $dark-gray;
          background-color: $light-gray;
          padding: 20px;
          width: 100vw;
          margin-left: calc((100% - 100vw) / 2);
        }
      }
    }
  }

  @include media-breakpoint-up(sm) {
    .image-grid {
      > p, > div {
        &[class*="col"] {
          max-width: calc(50% - 30px);
        }
        &.col-sm-4 {
          max-width: calc(33.3333% - 30px);
        }
        &.col-sm-3 {
          max-width: calc(25% - 30px);
        }
      }
    }
  }

  @include media-breakpoint-up(md) {
    .large-text {
      font-size: 1.5rem;
    }
    main {
      font-size: 1.125rem;
      .page-header {
        height: 320px;
        .page-image {
          position: absolute;
          height: 320px;
        }
        h1 {
          position: relative;
          font-size: 3rem;
          padding-top: .5em;
        }
        .intro {
          font-size: 1.75rem;
          .quote-attribute {
            font-size: 1.25rem;
          }
        }
      }

      h2 {

      }
    }
  }

  @include media-breakpoint-up(lg) {
    main {
      .page-header {
        //height: 520px;
        .page-image {
          //height: 520px;
        }
        h1 {
          //font-size: 3.75rem;
        }
        .intro {
          //font-size: 2.6rem;
          .quote-attribute {
            //font-size: 1.75rem;
          }
        }
      }

      h2 {
        font-size: 2.2rem;
      }

      h3 {
        font-size: 2rem;
      }

      .callout {
        font-size: 3rem;
      }
    }
  }
}



