.sidebar-primary {
  .content {
    @extend .row;
    margin: 0 auto;
    padding: 0;
    .main {
      @extend .col-sm-8;
    }
    .sidebar {
      @extend .col-sm-4;
      font-size: 1rem;
      .widget {
        h3 {
          font-size: 1.5rem;
          text-transform: uppercase;
          letter-spacing: 2px;
        }
      }
      .widget_categories {
        ul {
          list-style: none;
          margin: 0;
          padding: 0;
          li {
            margin-bottom: 5px;
            //text-transform: uppercase;
            a {
              font-weight: 600;
            }
          }
        }
      }
    }
  }
  @include media-breakpoint-up(sm) {
    .content {
      .sidebar {
        padding: 30px 0 0 40px;
      }
    }
  }
}



